html, body {
  background-color: black;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 100;
}

#clock {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 47% 6% 47%;
  justify-items: center;
  margin: auto;
  font-size: 38vw;
  color: white;
}

#hours,
#minutes {
  margin: auto;
  width: 100%;
}

#hours { 
  text-align: right;
}

#minutes {
  text-align: left;
}

.ampm {
  font-size: 3vw;
  margin: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.pulsate {
  animation: pulse 3s ease-out infinite;
  opacity: 0.15;
  font-weight: 200;
}

@keyframes pulse {
  0% {
    opacity: 0.15;
    font-weight: 200;
  }
  50% {
    opacity: 0.75;
    font-weight: 400;
  }
  100% {
    opacity: 0.15;
    font-weight: 200;
  }
}